import React from "react";
import { Route } from "react-router-dom";
import FirstFeature from "../FirstFeature/FirstFeature";
import SecondFeature from "../SecondFeature/SecondFeature";
import ThirdFeature from "../ThirdFeature/ThirdFeature";
import Home from "../Home/Home";
import About from "../About/About";
import "./Page.scss";

const Page = () => (
	<section className="page">
		<Route exact path="/" component={Home} />
		<Route path="/one" component={FirstFeature} />
		<Route path="/two" component={SecondFeature} />
		<Route path="/three" component={ThirdFeature} />
		<Route path="/about" component={About} />
	</section>
);

export default Page;
